import Vue from "vue";

import { CONSOLE_REQUEST_ENABLE, CONSOLE_RESPONSE_ENABLE } from "../index.js";

const vue = new Vue();

export const requestSuccessProcessor = config => {
	CONSOLE_REQUEST_ENABLE &&
		console.info("requestSuccessProcessor", `url: ${config.url}`, config);
	console.log(config);
	return config;
};

export const requestErrorProcessor = error => {
	return Promise.reject(error);
};

export const responseSuccessProcessor = response => {
	CONSOLE_RESPONSE_ENABLE && console.info("responseSuccessProcessor", response);

	let resData = response.data || {};

	let { returnCode, message } = resData;

	switch (returnCode) {
	case 0: // 业务处理成功，返回数据
		return resData.result;

	case 100100001: // 业务处理失败，弹窗提示，error: request error
		vue.$toast.show({
			text: message || "request error"
		});
		return null;

	case 100100002: // error: 服务异常
		vue.$toast.show({ text: message || "服务异常" });

		return null;

	case 100100004: // error: 业务出错
		vue.$toast.show({
			text: message || "业务出错"
		});

		return null;

	default:
		// 其他特殊业务逻辑
		!response.config.noShowDefaultError &&
				window.GLOBAL.eventBus.$emit("global.dialog.show", resData.msg); // 下放处理逻辑到业务层处理
		// 业务层捕获处理逻辑实例, App.vue
		// methods: {
		//   bindEvents() {
		//     GLOBAL.vbus.$on('global.dialog.show', (msg) => {
		//         if(!msg) return
		//         // 注册操作视图层的事件
		//         this.$dialog.popup({
		//             content: msg
		//         })
		//     })
		// }
		// }
		return Promise.reject(resData);
	}
};

export const responseErrorProcessor = error => {
	return Promise.reject(error);
};



