
import axios from '@/plugins/axios'
import myMixin from '@/plugins/mixins'
import Vue from 'vue'
import moment from 'moment'
import { getDateList, validator } from '@/service/common'

import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'



import MyCell from '@/components/MyCell.vue'
import SwCell from '@/components/SwCell.vue'
import { setTimeout } from 'timers';
Vue.component(MyCell.name, MyCell)
Vue.component(SwCell.name, SwCell)


export default {
mixins: [myMixin],
directives: {
TransferDom
},

components: {

XHeader,
GroupTitle,
Group,
Cell,
CellFormPreview,
Swiper,
SwiperItem,
Datetime,
XButton,
Rater,
CellBox,
Checker,
CheckerItem,
Popup,
XInput

},

data() {

return {
title: '刹车制动异常',
count: 2,
faultItems: [
[
{
label: '可能原因：',
value: '刹车片变薄'
}, {
label: '建议：',
value: '检修刹车片'
}, {
label: '预计费用：',
value: '200元'
}],
[
{
label: '可能原因：',
value: '刹车片变薄'
}, {
label: '建议：',
value: '检修刹车片'
}, {
label: '预计费用：',
value: '200元'
}],
],
date: moment().format("YYYY-MM-DD"),
dateList: getDateList(moment().format("YYYY-MM-DD"), 6),
moment: moment,
show: false,
dealer: {
dealerId: '',
dealerName: '',
distance: 0,
position: '',

starLevel: 0
},
swiperIndex: 0,
orderItems: [
{
time: "08:00 - 12:00",
rebate: "无折扣"
},
{
time: "13:00 - 18:00",
rebate: "无折扣"
}],
recommendDealerParams: {
vin: '车架号',
seriesId: '4045',
specId: '24182',
mapLongitudeBaidu: '116.76',
mapLatitudeBaidu: '39.6',
rdata: '20170720',
mile: '123',
code: '123'
},
recommendId: '',
contacts: '',
mobile: '',
code: '',
leftTime: 60,
getCodeDisabled: false,
codeContent: '获取验证码',
}

},

methods: {
async getRecommendDealer() {
let params = {
vin: '车架号',
seriesId: '4045',
specId: '24182',
mapLongitudeBaidu: '116.76',
mapLatitudeBaidu: '39.6',
rdata: '20170720',
mile: '123',
code: '123'
}
let dealer = await axios.get('/v1/dealer/recommend', params)
this.dealer = dealer || this.dealer
this.dealer.starLevel *= 1
this.recommendId = dealer.recommendId
console.log(dealer)
},
async getDealerInfo() {
return await axios.get(`/v1/dealer/${this.dealer.id}`)

},
showPlugin() {
let _this = this;

_this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD',
startDate: moment().format("YYYY-MM-DD"),

onConfirm(selectedDate) {
_this.date = selectedDate
if (moment(_this.dateList[5].date).isBefore(selectedDate) ||
moment(_this.dateList[0].date).isAfter(selectedDate)) _this.dateList = getDateList(selectedDate, 6)

},
onShow() {

},
onHide() {

}
})

},
handleOrder() {
this.show = true;
},
getCode() {
if ('' === this.mobile) {
this.$vux.toast.show({
text: '手机号不能为空',
type: 'warn',
width: '3rem'
})
return false
}
if (!validator.checkMobilePhone(this.mobile)) {
this.$vux.toast.show({
text: '手机号码格式有误',
type: 'warn',
width: '3rem'
})
return false
}
const timeout = () => {
if (this.leftTime > 0) {
this.getCodeDisabled = true
this.codeContent = `已发送(${this.leftTime})`
this.leftTime--
setTimeout(timeout, 1000)
}
else {
this.getCodeDisabled = false
this.codeContent = `获取验证码`
this.leftTime = 60
}
}
timeout()
},

async submitOrder() {
if ('' === this.mobile) {
this.$vux.toast.show({
text: '手机号不能为空',
type: 'warn',
width: '3rem'
})
return false
}
if (!validator.checkMobilePhone(this.mobile)) {
this.$vux.toast.show({
text: '手机号码格式有误',
type: 'warn',
width: '3rem'
})
return false
}
if ('' === this.code) {
this.$vux.toast.show({
text: '请输入验证码',
type: 'warn',
width: '3rem'
})
return false
}
this.$vux.loading.show({
text: '预约中'
})
this.show = false
let bookInfo = axios.post(`/v1/order/book`,{
recommendId: this.recommendId,
contacts: this.contacts || "测试用户名",
mobile: this.mobile,
bookTime: moment(this.date).format("YYYYMMDD")
})
setTimeout(() => {
this.$vux.loading.hide()
this.$router.push({path: '/fault/feedback', query: {result: bookInfo !== null ? 'success' : 'failed' }})
}, 1000)

}
},
created() {
this.getRecommendDealer()
}

}



