import moment from "moment";
import { weekdays } from "@/service/const";

export const getDateList = (startDate, count) => {
	let today = moment().format("YYYY-MM-DD");

	let tomorrow = moment(today).add(1, "days");

	let dateList = [];

	for (let i = 0; i < count; i++) {
		let currentDate = moment(startDate).add(i, "days");

		let weekday = null;

		if (moment(today).isSame(currentDate)) weekday = "今天";
		else if (moment(tomorrow).isSame(currentDate)) weekday = "明天";
		else weekday = weekdays[moment(currentDate).weekday()];

		dateList.push({
			date: currentDate.format("YYYY-MM-DD"),

			weekday: weekday
		});
	}

	return dateList;
};

export const validator = {
	checkMobilePhone(mobile) {
		return /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(
			mobile
		);
	}
};



